<template>
  <h1>Age of Sigmar army picker</h1><br>
    <div class="largebody">
      <p>This test is designed to give you suggestions regarding which AoS armies might be the best for you. If you have any suggestion for improvement, feel free to contact u/miloceane / @miloceane on Instagram or Github.
      </p>
      <div class="questions">
        <div class="question" id="question">
          {{ questions_data[current_question].question }}<br><br>
          <ul>
            <div v-for="(answer, index) in questions_data[current_question].answers" :key="answer">
              <li><button @click="compute_results(index)">{{ answer.text }}</button></li>
            </div>
            <li><button @click="compute_results(-1)">None of the above</button></li>
          </ul>
        </div>
      </div>
      <div class="results">
        <div v-if="results_exist" :style="{ 'background-image': 'url(' + require('../assets/img/'+sorted_results[0]['key']+'.png') + ')'}" class="results_display">
          <b>Aesthetics that match your answers the best:</b><br><br>
          <div v-for="(aesthetic, index) in sorted_results" :key="aesthetic" class="bar" :style="{ width: (100 * sorted_results[index]['val'] / sorted_results[0]['val']) + '%'}">
            <div v-if="sorted_results[index]['val'] > 0" class="name">
              {{ aesthetics_data[sorted_results[index]["key"]].name }}
            </div>
          </div>
          <img :src="require('../assets/img/'+sorted_results[0]['key']+'.png')" :alt="sorted_results[0]['key']" class="result_image" />
        </div>
        <div v-else>
          <b>No result yet!</b>
        </div>
      </div>
    </div>
    
</template>

<script>
  import aestheticsData from "../assets/data/aesthetics.json";
  import questionsData from "../assets/data/questions.json";
  
  export default {
    name: 'ArmyPicker',
    data() {
      return {
        aesthetics_data: aestheticsData,
        questions_data: questionsData,
        current_question: 0,
        all_results: {},
        sorted_results: [],
        results_exist: false,
        // armies_names: {},
        // disabled_alignment: false,
        // alignment: "",
        // disabled_race: false,
        // race_choice: [],
        // disabled_playstyle: false,
        // fav_playstyle_1: "",
        // fav_playstyle_2: "",
        // fav_playstyle_3: "",
        // bulkiness: 42,
        // seriousness: 42,
        // army_theme_rate: {},
      }
    },
    methods: {
      results_not_null() {
        let found_result = false;
        Object.keys(this.all_results).forEach(key => {
          if (this.all_results[key] > 0) 
          { 
            found_result = true; }
        });
        return found_result;
      },

      compute_results() {
        this.questions_data[this.current_question].answers.foreach((aesthetic) => {
          this.all_results[aesthetic.id]++;
        });

        this.results_exist = this.results_not_null();
        this.sorted_results = this.sort_results(this.all_results);
        return this.all_results;
      },

      sort_results(results) {
        let results_array = [];

        Object.keys(results).forEach(name => results_array.push({"key": name, "val":results[name]}));
        results_array = this.quicksort_results(results_array);
        results_array.forEach(val => this.sorted_results[val["key"]] = val["val"]);
        return results_array;
      },

      quicksort_results(results_array) {
        if (results_array.length <= 1) {
          return results_array;
        }

        var pivot = results_array[0]["val"];

        var left = []; 
        var right = [];

        for (var i = 1; i < results_array.length; i++) {
          var val_curr = results_array[i]["val"];
          val_curr > pivot ? left.push(results_array[i]) : right.push(results_array[i]);
        }

        return this.quicksort_results(left).concat(results_array[0], this.quicksort_results(right));
      }
    },
    // created() {
    //   //this.init_theme_rate();
    // },
    // watch: {
    //   alignment(new_alignment) {
    //     console.log("alignment changed");
    //     this.compute_results();
    //     return new_alignment;
    //   }
    // }
  };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  
  @import "../css/armypicker.css";
  
</style>